(function() {
  'use strict';

  angular
    .module('palazzoLuce')
    .directive('fsvsLibrary', fsvsLibrary);

  /** @ngInject */
  function fsvsLibrary(fsvs) {
    var directive = {
      restrict: 'A',
      link: linkFunc
    };

    return directive;

    function linkFunc(scope) {
      fsvs.rebind();
      scope.$on('$destroy', function(){
        fsvs.unbind();
      });
    }
  }

})();

(function() {
  'use strict';

  angular
    .module('palazzoLuce')
    .directive('closeMenu', closeMenu);

  /** @ngInject */
  function closeMenu() {
    var directive = {
      restrict: 'A',
      link: linkFunc
    };

    return directive;

    function linkFunc(scope, el) {

      var target = angular.element("#menuToggle input");
      el.click(function(){
        target.trigger('click');
      });
    }
  }

})();

(function() {
  'use strict';

  angular
    .module('palazzoLuce')
    .directive('angleTrigger', angleTrigger);

  /** @ngInject */
  function angleTrigger(fsvs) {
    var directive = {
      restrict: 'A',
      link: linkFunc
    };

    return directive;

    function linkFunc(scope, el) {
      el.click(function(){
        fsvs.slideDown();
      });
    }
  }

})();

